// Bootstrap modal
function showMobileInfoModal(event) {
    var $modal = $('#hc_modal_mobile_tooltip');
    $modal
        .find('.modal-body')
        .html('');

    // Clear the existing text
    var $button = $(event.relatedTarget);// Button that triggered the modal
    var content = $button.data('modal-text');// Extract info from data-* attributes
    $modal.find('.modal-body').html(content);
}

// Update the modal's content. We'll use jQuery here, but you could use a data binding library or other methods instead.
$(document).on('show.bs.modal', '#hc_modal_mobile_tooltip', showMobileInfoModal);

